<template>
  <v-flex xs12>
    <v-layout xs12 row>
      <v-checkbox
        v-if="toPredict.length > 0"
        label="Main"
        v-model="isActive.toPredict"
      ></v-checkbox>
      <v-checkbox
        v-if="prediction.length > 0"
        label="Prediction"
        v-model="isActive.prediction"
      ></v-checkbox>
      <v-checkbox
        v-for="(timeSerie, i) in timeSeries"
        :key="i"
        :label="'timeSerie-' + i"
        v-model="isActive[i]"
      ></v-checkbox>
    </v-layout>
    <svg :width="width" :height="height" class="grey darken-3">
      <g :transform="`translate(${marginLeft}, ${marginTop})`">
        <!-- linea a predecir (blue) -->
        <c-path
          v-if="isActive.toPredict"
          :rangeX="[0, total.length - 1]"
          :dataset="toPredict"
          strokeWidth="4"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- linea de prediccion (yellow) -->
        <c-path
          v-if="total.length > 0 && isActive.prediction"
          :transform="`translate(${$toXPixel(toPredict.length - 1, 0, total.length - 1, chartWidth)}, 0)`"
          color="yellow"
          strokeWidth="4"
          :rangeX="[0, total.length - 1]"
          :rangeY="[
            $getMin(total, 'y'),
            $getMax(total, 'y')
          ]"
          :dataset="prediction"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- timeSeries -->
        <c-path
          v-for="(timeSerie, i) in timeSeries"
          :key="i"
          v-if="isActive[i]"
          color="#ffbf8bb8"
          :rangeX="[0, total.length - 1]"
          :dataset="timeSerie"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- marcas de anomalias -->
        <line 
        v-for="(line, i) in anomalies"
        :key="'mark' + i"
        :x1="line" y1="0" :x2="line" :y2="chartHeight"
        stroke-linejoin="round"
        stroke-linecap="round"
        stroke-width="1.5"
        stroke="red"/>
        <!-- ejes -->
        <x-axis :range="[0, total.length - 1]" :ticks="10" :fixed="1" :height="chartHeight" :width="chartWidth"></x-axis>
        <y-axis :range="[globalMinY, globalMaxY]" :ticks="5" :fixed="1" :height="chartHeight" label="Units"></y-axis>
      </g>
    </svg>
  </v-flex>
</template>

<script>
export default {
  props: {
    dataSet: null,
    height: {
      type: Number,
      default: 500
    },
    marginLeft: {
      type: Number,
      default: 40
    },
    marginTop: {
      type: Number,
      default: 40
    },
    marginRight: {
      type: Number,
      default: 20
    },
    marginBottom: {
      type: Number,
      default: 30
    },
    padding: {
      type: Number,
      default: 12
    }
  },
  data: () => ({
    width: null,
    chartWidth: 1000,
    chartHeight: 400,
    total: [],
    toPredict: [],
    prediction: [],
    timeSeries: [],
    anomalies: [],
    globalMaxY: -10e10,
    globalMinY: 10e10,
    isActive: {
      toPredict: true,
      prediction: true
    }
  }),
  mounted () {
    this.width = this.$el.clientWidth
    this.chartWidth = this.width - (this.marginLeft + this.marginRight)
    this.chartHeight = this.height - (this.marginTop + this.marginBottom)
  },
  methods: {
    processData (d) {
      let yValues = []
      for (let i = 0; i < d.toPredict.length; i++) {
        this.toPredict.push({
          x: i,
          y: d.toPredict[i]
        })
        this.total.push({
          x: i,
          y: d.toPredict[i]
        })
        yValues.push({v: d.toPredict[i]})
      }
      this.toPredict.push({
        x: d.toPredict.length,
        y: d.prediction.future[0].value
      })
      for (let i = 0; i < d.prediction.future.length; i++) {
        this.prediction.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
        this.total.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
        yValues.push({v: d.prediction.future[i].value})
      }
      for (let t = 0; t < d.timeSeries.length; t++) {
        this.$set(this.timeSeries, t, [])
        for (let i = 0; i < d.timeSeries[t].data.length; i++) {
          this.timeSeries[t].push({
            x: i,
            y: d.timeSeries[t].data[i]
          })
          yValues.push({v: d.timeSeries[t].data[i]})
        }
        this.$set(this.isActive, t, true)
      }
      this.globalMaxY = this.$getMax(yValues, 'v')
      this.globalMinY = this.$getMin(yValues, 'v')

      for (let i = 0; i < d.prediction.past.length; i++) {
        let xMark = this.$toXPixel(d.prediction.past[i].step, 0, this.total.length, this.chartWidth)
        this.anomalies.push(xMark)
      }

      // let xMark = this.$toXPixel(this.toPredict.length - 2, 0, this.total.length - 1, this.chartWidth)
      // this.anomalies.push(xMark)

      // test de altura
      // console.log(this.$getMax(this.total, 'y'))
      // console.log(this.$getMax(this.timeSerie1, 'y'))
      // console.log(this.$getMax(this.timeSerie2, 'y'))
    },
    reset () {
      this.total = []
      this.toPredict = []
      this.prediction = []
      this.anomalies = []
      this.globalMaxY = -10e10
      this.globalMinY = 10e10
    }
  },
  watch: {
    dataSet: {
      handler: function (val) {
        if (val.toPredict && val.prediction) {
          this.$nextTick(() => {
            this.reset()
            this.processData(val)
          })
        }
      },
      immediate: true
    }
  }
}
</script>

<style>
  svg {
    user-select: none;
  }
</style>



