<template>
  <v-container grid-list-md fluid>
    <v-layout wrap>
      <v-flex xs8>
       <v-card flat class="transparent">
          <v-text-field label="url" v-model="url" outline></v-text-field>
          <v-textarea
            auto-grow
            :rules="[rules.isValidJSON]"
            v-model="dataSet"
            label="Data to process"
            outline
            @keypress.enter="getUrl"
          ></v-textarea>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="getUrl" color="green" flat>
              submit
              <v-icon right>send</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
        <line-chart :dataSet="toGraph" :height="350" :marginLeft="70"></line-chart>
      </v-flex>
      <v-flex xs4>
        <pre>{{response}}</pre>
      </v-flex>
    </v-layout>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card>
        <v-card-text>
          Processing...
          <v-progress-linear
            indeterminate
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="errorDialog.value"
      hide-overlay
      persistent
      width="500"
    >
      <v-card color="red" dark>
        <v-card-text>
          <pre>{{errorDialog.text}}</pre>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat @click="errorDialog.value = false">ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import resultTest from '@/assets/dataTest/multivariateResult'
import lineChart from '@/components/multivariateChart'
export default {
  components: {
    lineChart
  },
  data: () => ({
    url: 'http://54.93.223.206:9999/multivariate/get',
    dataSet: '',
    response: '',
    toGraph: {},
    loading: false,
    errorDialog: {
      value: false,
      text: ''
    },
    rules: {
      isValidJSON: v => {
        try {
          JSON.parse(v)
        } catch (e) {
          return 'Data is not a valid json'
        }
        return true
      }
    }
  }),
  methods: {
    getUrl () {
      this.loading = true
      const parseData = JSON.parse(this.dataSet)
      this.$http.post(this.url, parseData).then(response => {
        this.response = resultTest
        this.toGraph = {
          toPredict: parseData.main,
          prediction: this.response,
          timeSeries: parseData.timeseries
        }
        this.loading = false
      }).catch(err => {
        this.loading = false
        this.errorDialog.value = true
        this.errorDialog.text = err
        console.log(err)
      })
    },
    simulateResult (d) {
      this.response = resultTest
      const parseData = JSON.parse(d)
      this.toGraph = {
        toPredict: parseData.main,
        prediction: this.response,
        timeSeries: parseData.timeseries
      }
    },
    validateJSON (json) {
      try {
        JSON.parse(json)
      } catch (e) {
        return false
      }
      return true
    }
  }
  // watch: {
  //   dataSet: {
  //     handler: function (val) {
  //       this.simulateResult(val)
  //     },
  //     immediate: true
  //   }
  // }
}
</script>



