<template>
  <v-container grid-list-md fluid>
    <v-layout wrap>
      <v-flex xs8>
        <v-card flat class="transparent">
          <v-text-field label="url" v-model="url" outline></v-text-field>
          <v-textarea
            :rules="[rules.isValidJSON]"
            v-model="dataSet"
            label="Data to process"
            outline
            @keypress.enter="getUrl"
          ></v-textarea>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="getUrl" color="green" flat>
              submit
              <v-icon right>send</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
        <line-chart :dataSet="toGraph" :height="350" :marginLeft="70"></line-chart>
        <!-- <line-chart :dataSet="toGraph" :real="toGraphReal" :height="350" :marginLeft="70"></line-chart> -->
      </v-flex>
      <v-flex xs4>
        <pre>{{response}}</pre>
      </v-flex>
    </v-layout>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card>
        <v-card-text>
          Processing... this may take a while
          <v-progress-linear
            indeterminate
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="errorDialog.value"
      hide-overlay
      persistent
      width="500"
    >
      <v-card color="red" dark>
        <v-card-text>
          <pre>{{errorDialog.text}}</pre>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat @click="errorDialog.value = false">ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
// import prices from '@/assets/dataTest/23-02-2018-h14'
import resultTest from '@/assets/dataTest/univariateResult'
import lineChart from '@/components/timeLineChart'
export default {
  components: {
    lineChart
  },
  data: () => ({
    url: 'http://52.59.213.240:9999/univariate/get',
    dataSet: '',
    response: '',
    toGraph: {},
    toGraphReal: [],
    loading: false,
    errorDialog: {
      value: false,
      text: ''
    },
    rules: {
      isValidJSON: v => {
        try {
          JSON.parse(v)
        } catch (e) {
          return 'Data is not a valid json'
        }
        return true
      }
    }
  }),
  // mounted () {
  //   let pricesData = {data: []}
  //   for (let i = 0; i < 100; i++) {
  //     pricesData.data.push(prices[i].price)
  //   }
  //   this.dataSet = JSON.stringify(pricesData)
  //   for (let i = 0; i < 105; i++) {
  //     this.toGraphReal.push({y: prices[i].price})
  //   }
  // },
  methods: {
    getUrl () {
      this.loading = true
      const parseData = JSON.parse(this.dataSet)
      this.$http.post(this.url, parseData).then(response => {
        this.response = JSON.parse(response.bodyText)
        this.toGraph = {
          toPredict: parseData.data,
          prediction: this.response
        }
        this.loading = false
      }).catch(err => {
        this.loading = false
        this.errorDialog.value = true
        this.errorDialog.text = err
        console.log(err)
      })
    },
    simulateResult () {
      this.response = resultTest
      const parseData = JSON.parse(this.dataSet)
      this.toGraph = {
        toPredict: parseData.data,
        prediction: this.response
      }
    },
    validateJSON (json) {
      try {
        JSON.parse(json)
      } catch (e) {
        return false
      }
      return true
    }
  }
  // watch: {
  //   dataSet: {
  //     handler: function (val) {
  //       if (this.validateJSON(val)) {
  //         this.simulateResult()
  //       }
  //     },
  //     immediate: true
  //   }
  // }
}
</script>



