<template>
  <v-flex xs12>
    <svg :width="width" :height="height" class="grey darken-3"
    @mousewheel.prevent="zoom"
    @mousedown.exact="panEnabled = true"
    @mouseup="panEnabled = false"
    @mousemove="pan">
      <g :transform="`translate(${marginLeft}, ${marginTop})`" v-if="dataSet.toPredict && dataSet.prediction">
        <defs>
          <clipPath id="clip-rect">
            <rect :width="chartWidth" :height="height" :y="-this.marginTop" />
          </clipPath>
        </defs>
        <g clip-path="url(#clip-rect)">
        <!-- la real -->
          <c-path
          :transform="`translate(${-offsetX} 0)`"
          :rangeX="[zoomMin, zoomMax]"
          color="green"
          strokeWidth="4"
          :rangeY="[globalMin, globalMax]"
          :dataset="real"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- linea a predecir (blue) -->
        <c-path
          :transform="`translate(${-offsetX} 0)`"
          :rangeX="[zoomMin, zoomMax]"
          :rangeY="[globalMin, globalMax]"
          :dataset="total"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- linea de prediccion (yellow) -->
        <c-path
          v-if="total.length > 0"
          :transform="`translate(${$toXPixel(toPredict.length - 1 + zoomMin, zoomMin, zoomMax, chartWidth) - offsetX}, 0)`"
          color="yellow"
          :rangeX="[zoomMin, zoomMax]"
          :rangeY="[globalMin, globalMax]"
          :dataset="prediction"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>

        <!-- marcas de anomalias -->
        <line 
        v-for="(line, i) in anomalies"
        :key="i"
        :x1="line - offsetX" y1="0" :x2="line - offsetX" :y2="chartHeight"
        stroke-linejoin="round"
        stroke-linecap="round"
        stroke-width="1.5"
        stroke="red"/>
        <!-- ejes -->
        <x-axis
        :transform="`translate(${-offsetX} ${chartHeight})`"
        :range="[zoomMin, zoomMax]"
        :dataset="total"
        x="x"
        :ticks="25"
        :fixed="1"
        :height="chartHeight"
        :width="chartWidth"
        label="minutos"></x-axis>
        </g>
        <y-axis :range="[globalMin, globalMax]" :ticks="5" :fixed="1" :height="chartHeight" label="Units"></y-axis>
      </g>
    </svg>
  </v-flex>
</template>

<script>
export default {
  props: {
    real: null,
    dataSet: null,
    height: {
      type: Number,
      default: 500
    },
    marginLeft: {
      type: Number,
      default: 40
    },
    marginTop: {
      type: Number,
      default: 40
    },
    marginRight: {
      type: Number,
      default: 20
    },
    marginBottom: {
      type: Number,
      default: 30
    },
    padding: {
      type: Number,
      default: 12
    }
  },
  data: () => ({
    width: null,
    chartWidth: 1000,
    chartHeight: 400,
    total: [],
    toPredict: [],
    prediction: [],
    zoomMin: 0,
    zoomMax: 100,
    panEnabled: false,
    offsetX: 0,
    globalMax: -10e10,
    globalMin: 10e10
  }),
  mounted () {
    this.width = this.$el.clientWidth
    this.chartWidth = this.width - (this.marginLeft + this.marginRight)
    this.chartHeight = this.height - (this.marginTop + this.marginBottom)
  },
  methods: {
    processData (d) {
      for (let i = 0; i < d.toPredict.length; i++) {
        this.toPredict.push({
          x: i,
          y: d.toPredict[i]
        })
        this.total.push({
          x: i,
          y: d.toPredict[i]
        })
        this.globalMax = this.globalMax > d.toPredict[i] ? this.globalMax : d.toPredict[i]
        this.globalMin = this.globalMin < d.toPredict[i] ? this.globalMin : d.toPredict[i]
      }
      this.toPredict.push({
        x: d.toPredict.length,
        y: d.prediction.future[0].value
      })
      for (let i = 0; i < d.prediction.future.length; i++) {
        this.prediction.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
        this.total.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
        this.globalMax = this.globalMax > d.prediction.future[i].value ? this.globalMax : d.prediction.future[i].value
        this.globalMin = this.globalMin < d.prediction.future[i].value ? this.globalMin : d.prediction.future[i].value
      }
      if (this.real && this.real.length > 0) {
        for (let i = 0; i < this.real.length; i++) {
          this.globalMax = this.globalMax > this.real[i].y ? this.globalMax : this.real[i].y
          this.globalMin = this.globalMin < this.real[i].y ? this.globalMin : this.real[i].y
        }
      }
      this.zoomMax = this.total.length - 1
    },
    zoom (e) {
      if (this.zoomMin && this.zoomMax >= 3000) {
        this.zoomMin = 0
        this.zoomMax = this.total.length - 1
      } else {
        this.zoomMin += e.wheelDelta * 0.02
        this.zoomMax += -e.wheelDelta * 0.02
      }
    },
    pan (e) {
      if (this.panEnabled) {
        this.offsetX += -e.movementX
      }
    },
    reset () {
      this.total = []
      this.toPredict = []
      this.prediction = []
      this.globalMax = -10e10
      this.globalMin = 10e10
    }
  },
  watch: {
    dataSet: {
      handler: function (val) {
        if (val.toPredict && val.prediction) {
          this.$nextTick(() => {
            this.reset()
            this.processData(val)
          })
        }
      },
      immediate: true
    }
  },
  computed: {
    anomalies () {
      const d = this.dataSet
      if (d.toPredict) {
        let anomalies = []
        for (let i = 0; i < d.prediction.past.length; i++) {
          let xMark = this.$toXPixel(d.prediction.past[i].step, this.zoomMin, this.zoomMax, this.chartWidth)
          anomalies.push(xMark)
        }
        return anomalies
      }
    }
  }
}
</script>

<style>
  svg {
    user-select: none;
  }
</style>



