import Vue from 'vue'
import Router from 'vue-router'
import univariate from '@/views/univariate'
import multivariate from '@/views/multivariate'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'univariate',
      component: univariate
    },
    {
      path: '/multivariate',
      name: 'multivariate',
      component: multivariate
    }
  ]
})



