import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import vueCharts from './constructores/2d_graph'
import {
  Vuetify,
  VApp,
  VBtn,
  VIcon,
  VGrid,
  VTextField,
  VTextarea,
  VProgressLinear,
  VDialog,
  VCard,
  VCheckbox,
  transitions
} from 'vuetify'
import '../node_modules/vuetify/src/stylus/app.styl'

Vue.use(Vuetify, {
  components: {
    VApp,
    VBtn,
    VIcon,
    VGrid,
    VTextField,
    VTextarea,
    VProgressLinear,
    VDialog,
    VCard,
    VCheckbox,
    transitions
  }
})

Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(vueCharts)
Vue.http.headers.common['content-type'] = 'application/json'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



